ght: 4px solid var(--secondary-dark);\n  box-shadow: 2px 0 4px var(--secondary-dark);\n  max-height: 100vh;\n}\n\naside * {\n  color: black;\n}\n\nheader {\n  display: grid;\n}\n\n.logo {\n  place-self: center;\n}\n\n.logo > h1 {\n  position: relative;\n  z-index: 0;\n  margin: 0;\n  font-style: italic;\n  font-weight: 600;\n  font-size: 3rem;\n  color: black;\n  text-shadow: 0 0 4px var(--secondary-gold);\n}\n\n.logo > h1::before {\n  display: block;\n  position: absolute;\n  z-index: -1;\n  content: '';\n  height: 2rem;\n  width: 8rem;\n  background: var(--logo-gradient);\n  transform: rotate(-20deg) translate(-30px, 10px);\n  border-radius: 10%;\n  box-shadow: 0 0 4px var(--secondary-gold);\n}\n\n.logo > h1::after {\n  display: block;\n  position: absolute;\n  z-index: -1;\n  content: '';\n  height: 2rem;\n  width: 8rem;\n  background: var(--logo-gradient);\n  transform: rotate(-20deg) translate(30px, -35px);\n  border-radius: 10%;\n  box-shadow: 0 0 4px var(--secondary-gold);\n}\n\n.filters {\n  place-self: center start;\n  display: grid;\n  gap: 2rem;\n}\n\n.projects {\n  display: grid;\n  grid-template-rows: auto 1fr auto;\n}\n\n.projects h2 {\n  text-decoration: underline solid 4px var(--primary-gold);\n}\n\n.filter {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n  font-size: 1.5rem;\n  cursor: pointer;\n  transition: all .1s ease-in-out;\n}\n\n.filter::before {\n  position: absolute;\n  left: -10px;\n  content: '';\n  border: 0;\n  height: 110%;\n  width: 110%;\n  border-radius: 50%;\n  transform: rotate(-15deg);\n  transition: all 0.1s ease-in-out;\n}\n\n.filter::after {\n  position: absolute;\n  left: -10px;\n  content: '';\n  border: 0;\n  height: 105%;\n  width: 115%;\n  border-radius: 50%;\n  transform: rotate(20deg);\n}\n\n.filter:hover::after,\n.filter:hover::before {\n  border: 2px solid var(--primary-gold);\n}\n\n.filter.active::after,\n.filter.active::before {\n  border: 2px solid white;\n}\n\n.sortIcon {\n  height